.about {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.5rem;

  .image {
    flex: 1 1 40rem;

    img {
      width: 100%;
    }
  }

  .content {
    flex: 1 1 40rem;

    span {
      color: $green;
      font-size: 2.5rem;
    }

    h3 {
      color: $black;
      font-size: 3rem;
      padding-top: 0.5rem;
    }

    p {
      color: $light-color;
      font-size: 1.4rem;
      line-height: 2;
      padding: 1rem 0;
    }
  }
}

.gallery .box-container {
  @include grid(30rem);

  .box {
    height: 25rem;
    overflow: hidden;
    position: relative;

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }

    &:hover img {
      transform: scale(1.2);
    }

    &:hover .icons {
        bottom: 0;
    }

    .icons {
      text-align: center;
      background: rgba(0, 0, 0, 0.7);
      padding: 1.5rem 0;
      position: absolute;
      bottom: -6rem;
      left: 0;
      right: 0;
      z-index: 10;

      a {
        font-size: 2.5rem;
        color: $white;
        margin: 0 1.5rem;

        &:hover {
          color: $green;
        }
      }
    }
  }
}
